import * as Pixi from "pixi.js";
import 'pixi-particles';

var config = require('./../../www/particles/confetti.json');

export default class ConfettiParticles extends Pixi.Container
{
    emitter: Pixi.particles.Emitter;

    constructor(gold: boolean = false)
    {
        super();

        this.emitter = new Pixi.particles.Emitter(
            this,
            gold ? [
                Pixi.Texture.fromImage('particles/paper_1.png'),
            ] : [
                // Pixi.Texture.fromImage('particles/paper_1.png'),
                Pixi.Texture.fromImage('particles/paper_2.png'),
                Pixi.Texture.fromImage('particles/paper_3.png'),
                // Pixi.Texture.fromImage('particles/paper_4.png'),
            ],
            config
        );

        this.emitt = false;
    }

    public set emitt(value:boolean)
    {
        this.emitter.emit = value;
    }

    public update(deltaTime:number)
    {
        this.emitter.update(deltaTime / 70);
    }
}

var stars = require('./../../www/particles/stars.json');

export class StarsParticles extends Pixi.Container
{
    emitter: Pixi.particles.Emitter;

    constructor(image?: Pixi.Texture)
    {
        super();

        this.emitter = new Pixi.particles.Emitter(
            this,
            [
                // Pixi.Texture.fromImage('particles/alpha.png'),
                Pixi.Texture.fromImage('particles/star_07.png'),
                Pixi.Texture.fromImage('particles/star_08.png'),
            ],
            stars
        );

        this.emitt = false;

        const filter = new PIXI.filters.ColorMatrixFilter()
        filter.blendMode = Pixi.BLEND_MODES.ADD;
        this.filters = [filter];
    }

    public set emitt(value:boolean)
    {
        this.emitter.emit = value;
    }

    public update(deltaTime:number)
    {
        this.emitter.update(deltaTime / 70);
    }
}