import OneButtonScene from './OneButtonScene';
import SkipButton from '../gameobjects/SkipButton';
import { TweenLite, Expo } from "gsap";
import { GrabberAnimations } from '../gameobjects/Grabber';
import { BallPitAnimations } from '../gameobjects/BallPit';

export default class TwoButtonScene extends OneButtonScene
{
    skipButton:SkipButton;

    constructor()
    {
        super();

        this.grabButton.y -= 100;

        this.skipButton = new SkipButton();
		this.skipButton.position.set(1080, 380)
		this.skipButton.on('pointerdown', this.handleSkipClick.bind(this));
        this.addChild(this.skipButton);

        this.skipButton.active = false;
    }

    ready()
    {
        super.ready();

        this.skipButton.active = true;
    }

    hideButtons()
    {
        super.hideButtons();

        TweenLite.to(this.skipButton.position, 1, { x: 1500, ease: Expo.easeInOut }).delay(0.1);
    }

    handleGrabClick()
    {
        super.handleGrabClick();

        this.skipButton.active = false;
    }

    handleSkipClick()
    {
        this.hideButtons();

        this.move = false;
        this.skipButton.active = false;
        this.grabButton.active = false;

        this.grabber.play(GrabberAnimations.GRABBER_SKIP_RETRACT);

		setTimeout(() => {
            this.ballpit.play(BallPitAnimations.STEPS_ASCEND);
            setTimeout(() => {
                this.reveal();
            }, 1500);
		}, 500);
    }

    update(deltaTime:number)
    {
        super.update(deltaTime);

        this.skipButton.update(deltaTime);
    }
}