import * as PIXI from 'pixi.js'
import 'pixi-spine';

import {Assets} from './Assets';
import BasicScene from './scenes/BasicScene';
import OneButtonScene from './scenes/OneButtonScene';
import TwoButtonScene from './scenes/TwoButtonScene';
import ThreeButtonScene from './scenes/ThreeButtonScene';
import GoldBallScene from './scenes/GoldBallScene';
import KingScene from './scenes/KingScene';

import * as QueryString from 'query-string';

export default class GrabberPOC extends PIXI.Application
{
	public static readonly SCREEN_COLOR = 0x6495ed;
	public static readonly SCREEN_WIDTH = 1280;
	public static readonly SCREEN_HEIGHT = 720;

	private scene:BasicScene;

	constructor()
	{
		super({
			view: <HTMLCanvasElement>document.getElementById('canvas'),
			backgroundColor: GrabberPOC.SCREEN_COLOR,
			width: GrabberPOC.SCREEN_WIDTH,
			height: GrabberPOC.SCREEN_HEIGHT,
			resolution: 2
		});

		// Setup update loop
		this.ticker.add(this.update.bind(this));

		const background = PIXI.Sprite.fromImage(Assets.BACKGROUND);
		background.scale.set(0.5, 0.5);
        this.stage.addChild(background);

		const parsed = QueryString.parse(window.location.search);

		const scene = parsed.scene || 'one';

		if(scene == 'one') this.stage.addChild(this.scene = new OneButtonScene());
		if(scene == 'two') this.stage.addChild(this.scene = new TwoButtonScene());
		if(scene == 'three') this.stage.addChild(this.scene = new ThreeButtonScene());
		if(scene == 'gold') this.stage.addChild(this.scene = new GoldBallScene());
		if(scene == 'king') this.stage.addChild(this.scene = new KingScene());
	}

	update(deltaTime:number)
	{
		this.scene.update(deltaTime);
	}
}

PIXI.loader.reset();

PIXI.loader.add([
	Assets.BACKGROUND,
	Assets.BALLPIT,
	Assets.GRABBER,
	// Assets.TUNNEL,
	Assets.BALLPIT_RIM,
	Assets.BUTTON
]);

PIXI.loader.load(() => {
	new GrabberPOC();
});

// Hot Reload Code
if (module.hot) {
	module.hot.dispose(() => {
		window.location.reload();
	});
}