import * as Pixi from "pixi.js";
import OneButtonScene from "./OneButtonScene";
import ConfettiParticles from "../gameobjects/Particles";

export default class GoldBallScene extends OneButtonScene
{
    constructor()
    {
        super();

        this.grabber.spine.hackTextureBySlotName(
            'assets/grabber_ball_bottom',
            PIXI.Texture.fromImage('gold-ball.png'),
            new Pixi.Rectangle(0, 0, 1, 1)
        );

        this.grabber.spine.hackTextureBySlotName(
            'assets/grabber_ball_star',
            PIXI.Texture.fromImage('gold-star.png'),
            new Pixi.Rectangle(0, 0, 1, 1)
        );


        this.ballpit.spine.hackTextureBySlotName(
            'assets/grabber_ball_bottom',
            PIXI.Texture.fromImage('gold-ball.png'),
            new Pixi.Rectangle(0, 0, 1, 1)
        );

        this.ballpit.spine.hackTextureBySlotName(
            'Assets/grabber_ball_star',
            PIXI.Texture.fromImage('gold-star.png'),
            new Pixi.Rectangle(0, 0, 1, 1)
        );

        this.ballpit.spine.hackTextureBySlotName(
            'Assets/star_rotate_00',
            PIXI.Texture.fromImage('gold-win.png'),
            new Pixi.Rectangle(0, 0, 1, 1)
        );

        this.confetti = new ConfettiParticles(true);
        this.addChild(this.confetti);
    }
}