
import * as Pixi from "pixi.js";
import { TweenMax, TimelineMax, Sine } from "gsap";
import { Assets } from "../Assets";
import { Container, spine, loader } from 'pixi.js';

export enum GrabberAnimations {
    GRABBER_IDLE = 'grabber_idle',
    GRABBER_INTRO = 'grabber_intro',
    GRABBER_GRAB_BALL = 'grabber_grab_ball',
    GRABBER_DROP_RETRACT = 'grabber_drop_retract',
    GRABBER_SKIP_RETRACT = 'grabber_skip_retract'
}

export default class Grabber extends Container
{
    public spine:spine.Spine

    constructor()
    {
        super();

        const resource = loader.resources[Assets.GRABBER].spineData;
        this.spine = new spine.Spine(resource);
        this.spine.scale.set(0.5, 0.5);
        this.addChild(this.spine);

        this.play(GrabberAnimations.GRABBER_IDLE);

        // this.spine.hackTextureBySlotName(
        //     'assets/grabber_ball_star',
        //     Pixi.Texture.fromImage('cage-star.png'),
        //     new Pixi.Rectangle(0, 0, 1, 1)
        // );
    }

    public play(animation:GrabberAnimations, loop:boolean = false)
    {
        this.spine.state.setAnimation(0, animation, loop);
    }

    public zed()
    {
        const min = 0.98;
        const max = 1.12;

        var line = new TimelineMax();
        this.scale.x = this.scale.y = min;

        line.to(this.scale, 2, { x: max, y: max, ease: Sine.easeInOut })
        line.to(this.scale, 2, { x: min, y: min, ease: Sine.easeInOut })
        line.repeat(-1);
    }

    public zedKill()
    {
        TweenMax.killTweensOf(this.scale);
    }

    public scaleBack()
    {
        TweenMax.to(this.scale, 1, { x: 1, y: 1, ease: Sine.easeInOut });
    }
}