import { filters, Container, Sprite } from "pixi.js";

export default class GrabButton extends Container
{
    private glowFilter:filters.ColorMatrixFilter;
    public active:boolean;

    elaspedTime:number = 0;
    stength:number = -1;

    constructor()
    {
        super();

        const button = Sprite.fromImage('buttons/button.png');
        this.addChild(button);

        this.scale.set(0.5, 0.5);

        this.interactive = this.buttonMode = true;

        this.glowFilter = new filters.ColorMatrixFilter();
        this.filters = [ (this.glowFilter) ];
        this.active = false;
    }

    public update(deltaTime:number)
    {
        this.elaspedTime += deltaTime;
        const target = this.active ? 1 + (Math.sin(this.elaspedTime / 20) / 2) : -1;

        this.stength = this.lerp(this.stength, target, 0.05);

        this.glowFilter.contrast(this.stength);
    }

    public lerp(start: number, end: number, percent: number): number {
        return (start + percent * (end - start));
    }
}