import { Container } from "pixi.js";
import Grabber from './../gameobjects/Grabber';
import BallPit from './../gameobjects/BallPit';
import GrabberPOC from './../index';
import { Assets } from "../Assets";
import ConfettiParticles, { StarsParticles } from "../gameobjects/Particles";

export default class BasicScene extends Container
{
	grabber:Grabber;
	ballpit:BallPit;

	confetti:ConfettiParticles;
	stars:StarsParticles;

    constructor()
    {
        super();

        const mask = new PIXI.Graphics();
		mask.drawRect(0, 0, GrabberPOC.SCREEN_WIDTH, 550);
		this.addChild(mask);

		this.addChild(this.grabber = new Grabber());
		this.grabber.y = 0;
		this.grabber.x = 1280 / 2;
		this.grabber.mask = mask;

		this.ballpit = new BallPit();
		this.ballpit.position.set(GrabberPOC.SCREEN_WIDTH / 2, 560);
		this.ballpit.mask = mask;
		this.addChild(this.ballpit);

		const rim = PIXI.Sprite.fromImage(Assets.BALLPIT_RIM);
		rim.position.set((GrabberPOC.SCREEN_WIDTH / 2) - 363, 518);
		rim.scale.set(0.5);
		this.addChild(rim);


		this.addChild(this.confetti = new ConfettiParticles());
		this.confetti.position.set(0, -20);
		this.confetti.scale.set(1, 1);

		this.addChild(this.stars = new StarsParticles());
		this.stars.position.set(500, 200);
		this.stars.scale.set(0.5, 0.5);
    }

    public update(deltaTime:number)
    {
		this.confetti.update(deltaTime);
		this.stars.update(deltaTime);
    }
}