import BasicScene from "./BasicScene";
import GrabButton from "./../gameobjects/GrabButton";
import { TweenLite, Expo } from "gsap";
import { BallPitAnimations } from "../gameobjects/BallPit";
import { GrabberAnimations } from "../gameobjects/Grabber";
import GrabberPOC from "..";
import Button from "../gameobjects/Button";

export default class OneButtonScene extends BasicScene
{
    elapsedTime:number;
    grabButton:GrabButton;
    move:boolean;

    constructor()
    {
        super();

        this.elapsedTime = 0;

        this.grabButton = new GrabButton();
		this.grabButton.position.set(1080, 260)
		this.grabButton.on('pointerdown', this.handleGrabClick.bind(this));
		this.addChild(this.grabButton);

        TweenLite.to(this.grabber.position, 3, { y: 558, ease: Expo.easeInOut }).delay(0);

        this.ballpit.play(BallPitAnimations.INTRO);

        setTimeout(this.ready.bind(this), 2700);

        // const lights = new Button();
        // lights.position.set(1280 - 200, 400);
        // lights.scale.set(0.75, 0.75);
        // this.addChild(lights);
    }

    ready()
    {
        this.grabButton.active = true;
        this.move = true;
    }

    reveal()
    {
        this.ballpit.play(BallPitAnimations.STEPS_REVEAL_WIN)
        this.confetti.emitt = true;
        this.stars.emitt = true;
    }

    hideButtons()
    {
        TweenLite.to(this.grabButton.position, 1, { x: 1500, ease: Expo.easeInOut }).delay(0);
    }

    handleGrabClick()
    {
        this.grabButton.active = false;
        this.move = false;

        this.grabber.play(GrabberAnimations.GRABBER_GRAB_BALL);
        setTimeout(() => {
            TweenLite.to(this.grabber.position, 1, { x: GrabberPOC.SCREEN_WIDTH / 2, ease: Expo.easeInOut });
            setTimeout(() => {
                this.ballpit.play(BallPitAnimations.STEPS_ASCEND_EMPTY);

                this.hideButtons();

                // Swap Z-Index
                this.removeChild(this.grabber);
                this.addChild(this.grabber);

                TweenLite.to(this.grabber.position, 1, { y: 558, ease: Expo.easeInOut }).delay(1);

                setTimeout(() => {
                    this.grabber.play(GrabberAnimations.GRABBER_DROP_RETRACT);
                    setTimeout(() => {
                        this.removeChild(this.grabber);
                        this.reveal();
                    }, 1500);
                }, 2000);
            }, 1000);
        }, 2000);
    }

    public update(deltaTime:number)
    {
        super.update(deltaTime);

        this.grabButton.update(deltaTime);

        if(this.move) {
            this.elapsedTime += deltaTime
			this.grabber.x = (1280 / 2) + Math.sin((this.elapsedTime) / 50) * 230;
		}
    }
}